* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

@media screen and (max-width: 400px) {
  body {
    font-size: 10px;
  }
}

@media screen and (min-width: 800px) {
  body {
    font-size: 14px;
  }
}

html {
  font-size: calc( 10px + (14 - 10) * ((100vw - 400px) / ( 800 - 400)));
}

body {
  font-family: sans-serif;
  background: #ffffff;
}

.page {
  width: 90%;
  max-width: 1000px;
  margin: 3rem auto; /* margin: 3rem auto;  */
  box-shadow: 0 0 1rem rgba(0, 0, 0, .5);
  background: #fdfafd;
}


.menu {
  display: flex;
  width: 100%;
  max-width: 1000px;
  margin: 3rem auto;/*margin: 3rem auto;*/
  padding: 1em;
  border: 1px solid #0f0f0f;
  box-shadow: 0 0 1rem rgba(0, 0, 0, .5);
  background: #ece7ec;
  font-weight: 700;
}

.menu__item {
  position: relative;
  flex-grow: 1;
  text-align: right;
  
  /*line-height: 3rem;*/
  cursor: pointer;
  text-decoration: none;
  text-transform: uppercase;
  color: rgb(17, 17, 17);
 
  opacity: .6;
 /* border-bottom-width: .2rem;
  border-bottom-style: solid;
  border-bottom-color: transparent; */

  
}


